import { Component, OnInit } from '@angular/core';
import { AngularFireStorage } from '@angular/fire/compat/storage';
import { lastValueFrom } from 'rxjs';
import { map, finalize } from 'rxjs/operators';

@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.component.html',
  styleUrls: ['./add-item.component.scss'],
})
export class AddItemComponent implements OnInit {
  title = '';
  desc = '';
  size = '';
  retailPrice = '';
  askingPrice = '';
  titleError = '';
  askingPriceError = '';
  activeImage = 1;
  pic1Url = '';
  pic2Url = '';
  pic3Url = '';
  pic4Url = '';
  uploadSpinner = false;
  uploadErr = '';

  constructor(private storage: AngularFireStorage) {}

  ngOnInit(): void {}

  createListing() {
    this.titleError = '';
    this.askingPriceError = '';
    if (this.title.trim().length === 0) {
      this.titleError = 'Title is required';
    } else if (this.askingPrice.trim().length === 0) {
      this.askingPriceError = 'Asking Price is required';
    } else {

    }
  }

  onFileSelected(event: any) {
    var n = Date.now();
    const file = event.target.files[0];
    const filePath = `images/${n}`;
    const fileRef = this.storage.ref(filePath);
    const task = this.storage.upload(`images/${n}`, file);
    this.uploadSpinner = true;
    this.uploadErr = '';
    task.snapshotChanges().pipe(
      finalize(() => {
        fileRef.getDownloadURL().subscribe((downloadURL) => {
          if (this.activeImage === 1) {
            this.pic1Url = downloadURL;
            this.activeImage = 2;
          } else if (this.activeImage === 2) {
            this.pic2Url = downloadURL;
            this.activeImage = 3;
          } else if (this.activeImage === 3) {
            this.pic3Url = downloadURL;
            this.activeImage = 4;
          } else {
            this.pic4Url = downloadURL;
            this.activeImage = 5;
          }
          this.uploadSpinner = false;
        });
      })
    ).subscribe();
  }

  deleteImage(index: number) {
    let count = 0;
    if (index === 4) {
      this.pic4Url = "";
    } else if (index === 3) {
      this.pic3Url = this.pic4Url;
      this.pic4Url = "";
    } else if (index === 2) {
      this.pic2Url = this.pic3Url;
      this.pic3Url = this.pic4Url;
      this.pic4Url = "";
    } else if (index === 1) {
      this.pic1Url = this.pic2Url;
      this.pic2Url = this.pic3Url;
      this.pic3Url = this.pic4Url;
      this.pic4Url = "";
    }
    if (this.pic1Url !== "") {
      count++;
    }
    if (this.pic2Url !== "") {
      count++;
    }
    if (this.pic3Url !== "") {
      count++;
    }
    if (this.pic4Url !== "") {
      count++;
    }
    this.activeImage = count + 1;
  }
}
